import React, { Component } from "react";
import { TextInputOptions, TextInput } from "./TextInput";

export interface TextInputAndButtonOptions extends TextInputOptions {
    buttonContent?: string;
    onClick?: (text: string) => boolean | void;
}
export class TextInputAndButton extends Component<TextInputAndButtonOptions> {
    textInput = React.createRef<TextInput>()
    onClick() {
        if (this.props.onClick?.(this.textInput.current?.state.value ?? "")) this.textInput.current?.setState({ value: "" });
    }
    render() {
        return (
            <div className="text-input-and-button">
                <TextInput {...this.props} onEnter={() => this.onClick()} ref={this.textInput} />
                <button type="button" onClick={() => this.onClick()}>
                    {this.props.buttonContent} </button>
            </div>
        );
    }
}